import "./index.scss"; 

export default function Background() {

    return (
        <div className="background">
            <div className="background-container">
                <div className="background-column"></div>
                <div data-aos="fade-left" className="background-block-1 background-block"></div>
                <div data-aos="fade-top" className="background-block-2 background-block"></div>
            </div>
        </div>
    )
}